.three_d_btn {
  position: relative;
  display: inline-block;
  outline: none;
  vertical-align: middle;
  font-size: 20px;
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  padding: 1em 2em;
  border: 2px solid #f17704f9;
  border-radius: 1em;
  background: #F7941D;
  transform-style: preserve-3d;
  transition: all 175ms cubic-bezier(0, 0, 1, 1); }
  .three_d_btn img {
    margin-right: 6px;
    vertical-align: top; }
    @media (max-width: 991.98px) {
      .three_d_btn img {
        width: 21px;
        height: 21px;
        margin-right: 3px; } }
  @media (max-width: 991.98px) {
    .three_d_btn {
      padding: 15px 15px;
      font-size: 18px; } }
  @media (max-width: 575.98px) {
    .three_d_btn {
      padding: 12px 12px;
      font-size: 15px; } }
  .three_d_btn::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #F7941D;
    border-radius: inherit;
    box-shadow: 0 0 0 2px #f17704f9, 0 0.75em 0 0 rgba(203, 115, 7, 0.5);
    transform: translate3d(0, 0.75em, -1em);
    transition: all 175ms cubic-bezier(0, 0, 1, 1); }

button.three_d_btn:hover {
  background: #e98408;
  transform: translate(0, 0.375em); }

button.three_d_btn:hover::before {
  transform: translate3d(0, 0.75em, -1em); }

button.three_d_btn:active {
  transform: translate(0em, 0.75em); }

button.three_d_btn:active::before {
  transform: translate3d(0, 0, -1em);
  box-shadow: 0 0 0 2px #e98408, 0 0.25em 0 0 #e98408; }

